@import url("https://fonts.googleapis.com/css2?family=Lexend+Deca:wght@400;700&family=Outfit:wght@300;400;600&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #090b1a;
  color: white;
}

.container {
  width: max-content;
  background-color: #1b1938;
  display: flex;
  flex-direction: row-reverse;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.content-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1.5rem;
  width: 22rem;
  margin-left: 3rem;
  margin-right: 5rem;
}
.content-container span {
  color: #aa5cdb;
}
.content-container p {
  color: rgba(255, 255, 255, 0.75);
}
.content-container h1 {
  line-height: 2rem;
}

.img-container {
  width: 30.8rem;
  height: 25.5rem;
  background: linear-gradient(rgba(157, 0, 255, 0.5), rgba(157, 0, 255, 0.5)), url("../../images/image-header-desktop.jpg");
  background-size: contain;
  background-repeat: no-repeat;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.insight {
  display: flex;
  flex-direction: row;
  gap: 3rem;
  margin-top: 2rem;
}

.insight-num {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 20px;
}
.insight-num section {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400;
  font-size: 16px;
}

/*
===============================
   MEDIA QUERIES STARTS HERE 
===============================
*/
@media screen and (max-width: 910px) {
  body {
    overflow-x: hidden;
  }

  main {
    padding-top: 5rem;
  }

  .container {
    width: 20rem;
    background-color: #1b1938;
    display: flex;
    flex-direction: column;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    text-align: center;
  }

  .img-container {
    width: inherit;
    height: 16.6rem;
    background: linear-gradient(rgba(157, 0, 255, 0.5), rgba(157, 0, 255, 0.5)), url("../../images/image-header-desktop.jpg");
    background-size: contain;
    background-repeat: no-repeat;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 0;
  }

  .content-container {
    width: inherit;
    gap: 1.5rem;
    margin-left: 0;
  }
  .content-container h1 {
    line-height: 2rem;
    margin-top: 2rem;
  }

  .insight {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
  .insight .insight-num {
    font-size: 25px;
  }
  .insight .insight-num:first-child {
    text-transform: lowercase;
  }
  .insight .insight-num:first-child section {
    text-transform: uppercase;
  }
}
.attribution {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  color: white;
}
.attribution a {
  color: #aa5cdb;
  text-decoration: none;
}